import React from 'react';
import { connect } from 'react-redux';
import { setCurrentPage, setTotalUsers, follow, unfollow, getUsers } from '../../redux/reducer-users';
import Users from './Users';
import { compose } from 'redux';

class UsersAPIContainer extends React.Component {
    componentDidMount() {
        
        //with thunk
        this.props.getUsers(this.props.currentPage, this.props.pageSize)

        //with dispatch
        // this.props.togglePreloader(true)
        // userAPI.getUsers(this.props.currentPage, this.props.pageSize).then(data => {
        //     this.props.togglePreloader(false)
        //     this.props.setTotalUsers(response.data.totalCount);
        //     this.props.setUsers(data.items);
        // })
    }

    onPageChange = (page) => {

        //with thunk
        this.props.getUsers(page, this.props.pageSize)
        this.props.setCurrentPage(page)

        //with dispatch
        // this.props.setCurrentPage(page)
        // this.props.togglePreloader(true)

        // userAPI.getUsers(page, this.props.pageSize).then(data => {
        //     this.props.togglePreloader(false)
        //     this.props.setUsers(data.items);
        // })
    }

    render() {
        return <>
            <Users usersState={this.props.usersState}
                isFetching={this.props.isFetching}
                totalUsers={this.props.totalUsers}
                pageSize={this.props.pageSize}
                currentPage={this.props.currentPage}
                onPageChange={this.onPageChange}
                follow={this.props.follow}
                unfollow={this.props.unfollow}
                followingProgress={this.props.followingProgress}
            />
        </>
    }
}

const MapStateToProps = (state) => {
    return {
        usersState: state.usersPage.users,
        pageSize: state.usersPage.pageSize,
        totalUsers: state.usersPage.totalUsers,
        currentPage: state.usersPage.currentPage,
        isFetching: state.usersPage.isFetching,
        followingProgress: state.usersPage.followingProgress,
    }
}

export default compose(connect(MapStateToProps, {
    setTotalUsers,
    setCurrentPage,
    follow,
    unfollow,
    getUsers
}),
)(UsersAPIContainer)




